.title {
    font-family: fantasy;
    transform: skew(-0.01deg);
    font-size: 50px;
    color: white;
    text-shadow: 0 0 25px #000000,0 0 25px #000000,0 0 25px #000000,0 0 25px #000000;
}

.text {
    z-index: 0;
    font-family: GmarketM;
    transform: skew(-0.01deg);
    color: white;
    text-shadow: 0 0 10px #000000;
}

.btn {
    font-family: GmarketM;
    transform: skew(-0.01deg);
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    padding: 10px 20px;
    background-color: #211f1f;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.btn:hover {
    background-color: #141414;
}

@font-face {
    font-family: GmarketM;
    src: url('/fonts/GmarketMedium.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: GmarketL;
    src: url('/fonts/GmarketLight.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}