.title {
    font-family: fantasy;
    transform: skew(-0.01deg);
    font-size: 200px;
    color: white;
    text-shadow: 0 0 25px #000000;
}

.text {
    z-index: 0;
    font-family: GmarketM;
    transform: skew(-0.01deg);
    color: white;
    text-shadow: 0 0 10px #000000;
}

.btn {
    font-family: GmarketM;
    transform: skew(-0.01deg);
    background-color: #211f1f;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 40px;
    text-align: center;
}

.back {
    z-index: 999;
    font-family: GmarketM;
    transform: skew(-0.01deg);
    background-color: #211f1f;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    position: fixed;
    top: 10px;
    left: 10px;
    width: 40px;
    height: 40px;
    text-align: center;
}

.loading {
    font-family: fantasy;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-0.01deg);
    font-size: 50px;
    color: black;
    text-align: center;
    text-shadow: 0 0 25px #2a2a2a;
    position: fixed;
}

.back:hover {
    background-color: #141414;
}


.btn:hover {
    background-color: #141414;
}

@font-face {
    font-family: GmarketM;
    src: url('/fonts/GmarketMedium.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: GmarketL;
    src: url('/fonts/GmarketLight.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}